<template lang="html">
  <div class="add-imgs">
    <div class="add-imgs-btn clearfix">
      <label for="addImage">
        <span class="fr" name="">添加</span>
        <input @change="changeFile" type="file" id="addImage" multiple style="display:none">
      </label>
    </div>
    <div class="add-imgs-pics clearfix">

      <ul class="fileBoxUl clearfix">
        <li class="fl" v-for="(item, index) in imgArr">
          <label for="">
            <div class="viewThumb">
              <img :src="item" alt=""/>
            </div>
            <div @click="$emit('delete-image', index)" class="cancel"></div>
            <div class="model"></div>
          </label>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
export default {
  props: {
    imgArr: {
      type: Array,
      default () {
        return []
      }
    },
    sign: String
  },
  methods: {
    changeFile (e) {
      utils.uploadPic(e.target.files, (data) => {
        this.$emit('submit-pic', data, this.sign)
      })
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

.add-imgs {
  padding: 0 .22rem;
  background: @whiteColor;
  .add-imgs-btn {
    padding-bottom: .2rem;
    label {
      display: inline-block;
      float: right;
    }
    span {
      width: 2rem;
      height: .68rem;
      line-height: .68rem;
      border: none;
      border-radius: .05rem;
      background: @activeColor;
      color: @whiteColor;
      text-align: center;
    }
  }
  .add-imgs-pics {
    .fileBoxUl {
      position:relative;
      overflow:hidden;
    	margin: 0 auto;
      li {
        width: 35.7%;
        height: 2rem;
        overflow:hidden;
      	position:relative;
        margin-bottom: 0.3rem;
        &:last-child {
          width: 28.6%;
        }
        .viewThumb {
          position:absolute;
        	top:0;
        	left:0;
          width: 2rem;
          height: 2rem;
          overflow:hidden;
          border: 1px solid @activeColor;
          img {
            width: 2rem;
            height: 2rem;
          }
        }
        .cancel {
          position: absolute;
        	width: .44rem;
        	height: .44rem;
        	top: .05rem;
        	left: 1.54rem;
        	cursor:pointer;
        	z-index: 999;
        	opacity: .7;
          background:url(../../assets/img/recognize/close@2x.png) no-repeat;
          background-size: .44rem .44rem;
          // background-color: #fff;
        	display: block;
        }
        .model {
        	width: 2rem;
        	height: 2rem;
        	position:absolute;
        	top:0;
        	left:0;
        	background: rgba(0,0,0,.5);
        	z-index: 1;
        }
      }
    }


  }
}
</style>



