<template lang="html">
  <div class="">
    <default-head header="特殊行业资格认证"></default-head>
    <div id="wrapper" class="special-company-box">
      <div class="re-form-box">
        <flm-form-input label="公司名称" v-model="specialInfo.name"
        data-vv-name="公司名称" v-validate="'required'" name="name"
        placeholder="请输入公司名称"></flm-form-input>
      </div>
      <div class="re-form-box">
        <flm-form-input label="证件名称" v-model="specialInfo.certificate"
          data-vv-name="证件名称" v-validate="'required'" name="certificate"
         placeholder="特殊行业资格证全称, 非该类用户不可填"></flm-form-input>
      </div>
      <div class="pic-box f-re-martop">
        <img-block title="商标注册证(选填)" @submit-pic="getImages" sign="trademarkImg"
          :image="specialInfo.trademarkImg" message="商标注册证原件拍照, 商品持有人与公司名称保持一致"
        ></img-block>
      </div>
      <div class="pic-box">
        <img-block title="授权委托书(选填)" @submit-pic="getImages" sign="attorneyAuthImg"
          :image="specialInfo.attorneyAuthImg"  message="例: 商标使用权、生产委托书、经销商授权书等"
        ></img-block>
      </div>
      <div class="pic-box1">
        <img-block title="特殊行业资格证正面" @submit-pic="getImages" sign="specialAuthImgArray" type="moreone"
          message="例: 全国工业产品生产许可证, 保健品、化妆品、化学品生产许可证"
        ></img-block>
      </div>
      <add-imgs :imgArr="specialInfo.specialAuthImgArray" sign="specialAuthImgArray"
        data-vv-name="特殊行业资格证正面" v-validate="'required'" data-vv-value-path="imgArr"
        @submit-pic="getImages" @delete-image="deleteImage"></add-imgs>
      <div v-if="specialInfo.status === 2 && specialInfo.auditSpecRemark"
      class="recognize-remark">备注: {{specialInfo.auditSpecRemark}}</div>
    </div>
    <foot-btn @click.native="submitRecognize"></foot-btn>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import imgBlock from '@/components/recognize/imgBlock'
import addImgs from '@/components/recognize/addImgs'
import footBtn from '@/components/common/footBtn'
export default {
  components: {defaultHead, imgBlock, addImgs, footBtn},
  data () {
    return {
      specialInfo: {
        userId: 0,
        name: '',
        certificate: '',
        trademarkImg: '',
        attorneyAuthImg: '',
        specialAuthImgArray: [],
        specialAuthStatus: 0,
        auditSpecRemark: ''
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  mounted () {
    this.initPage()
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      this.ajaxGetCompanyInfo({userId: this.userId})
    },
    deleteImage (index) {
      this.specialInfo.specialAuthImgArray.splice(index, 1)
    },
    submitRecognize () {
      // 是否已经认证通过
      if (this.specialInfo.specialAuthStatus === 3) {
        minutils.MessageBox({message: '您的信息已经认证通过, 请勿重复提交'})
        return
      }
      if (this.specialInfo.specialAuthStatus === 1) {
        minutils.MessageBox({message: '您的信息还在审核中, 请勿重复提交'})
        return
      }
      this.$validator.validateAll().then(() => {
        this.ajaxSaveSpecailRecognize(this.specialInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    getImages (data, sign) {
      if (sign === 'specialAuthImgArray' && (this.specialInfo.specialAuthImgArray.length + data.length) > 3) {
        minutils.Toast('最多只能上传三张图片')
        return
      }
      data.forEach((item, index) => {
        this.ajaxUploadPic({fileName: item.fileName, fileBase64: item.base64Img}, sign)
      })
    },
    ajaxGetCompanyInfo (data) {
      minutils.IndicatorOpen('加载中...')
      api.getCompanyInfo(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data) {
          Object.keys(this.specialInfo).forEach(k => {
            if (result.data[k]) {
              this.specialInfo[k] = result.data[k]
            }
          })
        }
      })
    },
    ajaxSaveSpecailRecognize (data) {
      api.companySpecialAuthenticity(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          minutils.MessageBox({message: '保存特殊行业信息成功'})
        }
      })
    },
    ajaxUploadPic (data, sign) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          if (sign === 'specialAuthImgArray') {
            ((data, self) => {
              self.specialInfo.specialAuthImgArray.push(result.data)
            })(result.data, this)
          } else {
            this.specialInfo[sign] = result.data
          }
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/recognize/index.less';
.special-company-box {
  padding-bottom: 1.5rem;
}
.pic-box1 {
  background: @whiteColor;
  padding: .3rem .22rem;
}
</style>



